/**
 * Vue Router
 *
 * @library
 */

import Vue from 'vue'
import Router from 'vue-router'
import CarRouters from './car'
import ConnectRouters from './connect'

Vue.use(Router)

const ConstRouters = [
  {
    path: '/',
    name: 'Home',
    component: (resovle) => import('@/views/Home.vue').then(resovle)
  },
  {
    path: '/dashboard',
    component: (resovle) => import('@/views/Layout.vue').then(resovle),
    children: [
      {
        path: '',
        name: 'Dashboard',
        component: (resolve) => import('@/views/Dashboard.vue').then(resolve),
        meta: {
          authorized: false
        }
      }
    ]
  },
  {
    path: '/login',
    name: 'Login',
    component: (resovle) => import('@/components/Login').then(resovle)
  }
]

const routes = [
  ...ConstRouters,
  ...CarRouters,
  ...ConnectRouters,
  {
    path: '*',
    redirect: '/dashboard'
  }
]

// Create a new router
const router = new Router({
  // mode: 'history',
  routes: routes,
  scrollBehavior(to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition
    }
    if (to.hash) {
      return {
        selector: to.hash
      }
    }
    return {
      x: 0,
      y: 0
    }
  }
})

