import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    count: {
      aaa: 101,
      plat_msg: '',
      car_msg: ''
    }
  },
  // 有依赖的写在getters
  getters: {
    countnew: (state) => {
      return state.count.aaa + 11111111
    }
  },
  // 同步修改数据
  mutations: {
    fn1(state) {
      state.count.aaa++
    },
    fn2(state, paaaa) {
      console.log(paaaa)
    },
    plat_msg(state, msg) {
      state.plat_msg = msg
      console.log(state.plat_msg)
    },
    car_msg(state, msg) {
      state.car_msg = msg
      console.log(state.car_msg)
    }
  },
  // 异步修改数据
  actions: {}
})
